import { createApp } from 'vue'
import App from './App.vue'
import router from './router'
import store from './store'
import i18n from './i18n'
import ElementPlus from 'element-plus'
import 'element-plus/dist/index.css'
import * as ElementPlusIconsVue from '@element-plus/icons-vue'
import '@fortawesome/fontawesome-free/css/all.css'
import { initResizeObserverFix } from './utils/resizeObserverFix'

// 创建应用实例
const app = createApp(App)

// 初始化 ResizeObserver 错误处理
initResizeObserverFix()

// 立即拦截 webpack-dev-server 的错误处理
if (process.env.NODE_ENV === 'development') {
  // 覆盖 webpack-dev-server 的 handleError 函数
  if (typeof window !== 'undefined') {
    // 保存原始的 handleError
    const originalHandleError = window.handleError
    
    // 重写 handleError 函数
    window.handleError = function(error) {
      // 检查是否是 ResizeObserver 错误
      if (error && error.message && error.message.includes('ResizeObserver loop completed with undelivered notifications')) {
        // 静默处理，不显示错误
        return
      }
      
      // 对于其他错误，调用原始处理函数
      if (originalHandleError) {
        return originalHandleError.call(this, error)
      }
    }
    
    // 覆盖 webpack-dev-server 的 overlay 错误显示
    if (window.__webpack_dev_server_client__) {
      const client = window.__webpack_dev_server_client__
      if (client && client.overlay) {
        // 完全禁用 overlay
        client.overlay = {
          errors: false,
          warnings: false
        }
      }
    }
  }
}

// 全局错误处理
app.config.errorHandler = (err, vm, info) => {
  // 忽略ResizeObserver错误，这是Element Plus的已知问题
  if (err.message && err.message.includes('ResizeObserver loop completed with undelivered notifications')) {
    return
  }
  console.error('Vue Error:', err)
  console.error('Error Info:', info)
  console.error('Component:', vm)
}

// 处理ResizeObserver错误
const originalError = console.error
console.error = (...args) => {
  if (args[0] && typeof args[0] === 'string' && args[0].includes('ResizeObserver loop completed with undelivered notifications')) {
    return
  }
  originalError.apply(console, args)
}

// 处理webpack-dev-server的overlay错误
const originalWarn = console.warn
console.warn = (...args) => {
  if (args[0] && typeof args[0] === 'string' && args[0].includes('ResizeObserver loop completed with undelivered notifications')) {
    return
  }
  originalWarn.apply(console, args)
}

// 全局处理ResizeObserver错误
window.addEventListener('error', (event) => {
  if (event.message && event.message.includes('ResizeObserver loop completed with undelivered notifications')) {
    event.preventDefault()
    return false
  }
})

// 处理未捕获的Promise错误
window.addEventListener('unhandledrejection', (event) => {
  if (event.reason && event.reason.message && event.reason.message.includes('ResizeObserver loop completed with undelivered notifications')) {
    event.preventDefault()
    return false
  }
})

// 尝试禁用ResizeObserver
if (window.ResizeObserver) {
  const originalResizeObserver = window.ResizeObserver
  window.ResizeObserver = class extends originalResizeObserver {
    constructor(callback) {
      super((entries, observer) => {
        try {
          callback(entries, observer)
        } catch (error) {
          if (error.message && error.message.includes('ResizeObserver loop completed with undelivered notifications')) {
            // 忽略这个错误
            return
          }
          throw error
        }
      })
    }
  }
}

// 处理webpack-dev-server的错误
if (process.env.NODE_ENV === 'development') {
  // 覆盖webpack-dev-server的错误处理
  const originalHandleError = window.handleError
  if (originalHandleError) {
    window.handleError = (error) => {
      if (error && error.message && error.message.includes('ResizeObserver loop completed with undelivered notifications')) {
        return
      }
      return originalHandleError(error)
    }
  }
  
  // 尝试禁用webpack-dev-server的错误显示
  if (window.__webpack_dev_server_client__) {
    const client = window.__webpack_dev_server_client__
    if (client && client.overlay) {
      try {
        client.overlay = {
          ...client.overlay,
          errors: false,
          warnings: false
        }
      } catch (e) {
        // 忽略配置错误
      }
    }
  }
}

// 注册所有图标
for (const [key, component] of Object.entries(ElementPlusIconsVue)) {
  if (component) {
    app.component(key, component)
  }
}

// 按顺序使用插件
app.use(store)
app.use(router)
app.use(i18n)
app.use(ElementPlus, {
  // 配置Element Plus以减少ResizeObserver的使用
  size: 'default',
  zIndex: 3000,
  // 禁用某些可能导致ResizeObserver错误的特性
  experimentalFeatures: {
    // 禁用某些实验性功能
  }
})

// 全局配置 Element Plus 组件以减少 ResizeObserver 使用
if (typeof window !== 'undefined') {
  // 禁用 Element Plus 的自动调整大小功能
  window.__ELEMENT_PLUS_CONFIG__ = {
    size: 'default',
    zIndex: 3000,
    // 禁用自动调整大小
    autoInsertSpace: false,
    // 禁用自动聚焦
    autoFocus: false
  }
}

// 挂载应用
app.mount('#app') 